.grid {
  position: relative;
  height: calc(100% - 50px);
  width: 90%;
  margin: 0 auto; }

#scrolling-wrapper {
  position: absolute;
  z-index: 10;
  bottom: 0;
  height: 100%;
  left: 0;
  width: 100%;
  overflow: hidden;
  display: flex;
  @media all and (min-width: 1300px) {
    justify-content: center; }
  #scroller {
    position: absolute;
    z-index: 1;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    transform: translateZ(0);
    user-select: none;
    padding-left: 50px; } }


