.iScrollHorizontalScrollbar {
  left: 0 !important;
  right: 0 !important;
  &:before {
    content: '';
    background: $brand-orange;
    width: 100%;
    height: 2px;
    position: absolute;
    top: 50%;
    margin-top: -1px; } }


.iScrollIndicator {
  background: $brand-orange !important;
  border: 1px solid $brand-orange !important; }


.grid__item {
  position: relative;
  display: block;
  float: left;
  height: 360px;
  width: 320px;
  overflow: hidden;
  margin: 5px;
  &--green {
    background-color: rgba(green, 0.2); }
  &--red {
    background-color: rgba(red, 0.2); }
  @include phones {
    max-width: 320px; } }


.grid__item {
  @media all and (max-height: 800px) and (min-height: 600px) {
    margin-top: 100px; } }

h2.grid__title {
  position: absolute;
  background-color: rgba($white, 0.7);
  top: 30%;
  z-index: 2;
  padding-left: 1em;
  width: 50%;
  padding: 1em; }

.grid__link {
  position: absolute;
  bottom: 5%;
  z-index: 2;
  right: 0;
  background-color: rgba(#000, 0.6);
  display: table;
  padding: 5px;
  color: $brand-orange;
  svg {
    display: inline-block;
    vertical-align: middle;
    fill: $brand-orange; } }

.grid__link {
   @media all and (max-height: 480px) {
     top: 5%; } }

.grid__linkname {
  display: inline-block;
  vertical-align: middle;
  text-decoration: underline;
  margin-right: 5px; }


.grid__image img {
  display: block;
  position: absolute;
  top: 2em;
  right: 1em;
  border-radius: 5px 0 0 0;
  z-index: 1; }

.drag-popup-icon {
  display: none;
  svg {
    fill: $white; }
  @media all and (max-width: 1300px) {
    display: block;
    position: absolute;
    left: 0;
    bottom: 20px; } }
